@import url("https://fonts.googleapis.com/css?family=El+Messiri&display=swap");
body {
  font-family: "El Messiri", sans-serif, arial;
  background-color: #fff;
  font-size: 14px;
}
ul,
ol {
  list-style: none;
  padding-left: 0;
  padding-right: 0;
}

/*  scrollbar*/
/* width */
::-webkit-scrollbar {
  width: 18px;
}
/* Handle */
::-webkit-scrollbar-thumb {
  background: $orange1;
  border-left: 1px solid #eee;
}
/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: $orange2;
}
/*  // scrollbar*/



@media screen and #{$maxSmall} {
  body {
    font-size: 12px;
    padding-top: 80px;
  }
  .h3 {
    font-size: 1.2rem;
  }
}
