/* Card */
.card--custom {
  .card-head a .card-head__overlay {
    transition: all 0.3s ease-in-out;
    opacity: 0.3;
  }

  &:hover .card-head a .card-head__overlay {
    background-color: $orange1;
    width: 100%;
    height: 100%;
    position: absolute;
    left: 0;
    top: 0;
    right: 0;
  }
}
/* // Card */
/* Card Shop */
.card--shop {
  margin-bottom: 1rem;
  margin-top: 1rem;
  .card-head {
    position: relative;
    .shop-logo {
      border: 2px solid $orange1;
      border-radius: 50%;
      width: 35%;
      position: absolute;
      bottom: -10px;
      right: 15px;
      img {
        width: 100%;
        border-radius: 50%;
      }
    }
    .rate {
      position: absolute;
      left: 10px;
      bottom: 14px;
    }
  }

  .card-body {
    background-color: $gray3;
    border-top: 1.5px solid $orange1;
    .card-body__title {
      i {
        color: $orange1;
        font-size: 12px;
      }
      span {
        color: $gray2;
        font-size: 1rem;
        font-weight: bold;
      }
    }
    .card-body__addess,
    .card-body__phone {
      text-decoration: none;
      p {
        i {
          color: $orange1;
          font-size: 12px;
        }
        span {
          color: $gray2;
          font-size: 0.7rem;
          font-weight: bold;
        }
      }
    }

    .social {
      display: flex;
      margin: 3px 0 0 0;
      li {
        &:not(:last-of-type) {
          margin-inline-end: 10px;
        }
        i {
          font-size: 25px;
          color: $gray2;

          &:hover {
            color: $orange1;
            transition: color 0.3s ease-in-out;
          }
        }
      }
    }
  }
}
/* End Card Shop */
/* Card Shop */
.card--offer {
  margin-bottom: 1rem;
  margin-top: 1rem;
  .card-head {
    position: relative;

    a {
      .ribbon {
        width: 150px;
        height: 150px;
        overflow: hidden;
        position: absolute;
      }
      .ribbon::before,
      .ribbon::after {
        position: absolute;
        z-index: -1;
        content: "";
        display: block;
        border: 5px solid $orange1;
      }

      .ribbon span {
        position: absolute;
        display: block;
        width: 225px;
        padding: 15px 0;
        background-color: $orange1;
        box-shadow: 0 5px 10px rgba(0, 0, 0, 0.1);
        color: #fff;
        font: 700 18px/1 "Lato", sans-serif;
        text-shadow: 0 1px 1px rgba(0, 0, 0, 0.2);
        text-transform: uppercase;
        text-align: center;
      }

      /* top left*/
      .ribbon-top-left {
        top: -10px;
        left: -10px;
      }
      .ribbon-top-left::before,
      .ribbon-top-left::after {
        border-top-color: transparent;
        border-left-color: transparent;
      }
      .ribbon-top-left::before {
        top: 0;
        right: 0;
      }
      .ribbon-top-left::after {
        bottom: 0;
        left: 0;
      }
      .ribbon-top-left span {
        right: -25px;
        top: 30px;
        transform: rotate(-45deg);
      }
    }
    /* ---- */
    .sale-box {
      width: 100px;
      top: -8px;
      left: -7px;
      position: absolute;
      img {
        width: 100%;
      }
    }
    /* ---- */
  }

  .card-body {
    background-color: $gray3;
    border-top: 1.5px solid $orange1;
    .card-body__title {
      span {
        color: $gray2;
        font-size: 1rem;
        font-weight: bold;
      }
      .under-line {
        border-bottom: 2px solid $gray2;
      }
    }
    .card-body__addess,
    .card-body__phone {
      text-decoration: none;
      p {
        i {
          color: $orange1;
        }
        span {
          color: $gray2;
          font-size: 0.7rem;
          font-weight: bold;
        }
      }
    }
    .offer-container {
      text-align: left;

      .take-offer {
        text-decoration: none;
        color: $gray2;
        background-color: $orange1;
        padding: 2px 7px;
        font-size: 0.8rem;
        margin-top: 10px;
        display: inline-block;
        &:hover {
          color: $orange1;
          background-color: $gray2;
          transition: all 0.3s ease-in-out;
        }
      }
    }
  }
}
/* End Card Shop */
/* Card Events */
