  .card-head {
    position: relative;
    .event-date {
      position: absolute;
      width: 85px;
      top: 0;
      left: 0;
      img {
        width: 100%;
      }

      .event-date__m {
        position: absolute;
        top: 21px;
        left: 17px;
        font-size: 12px;
        color: #fff;
        font-weight: bold;
      }
