}
/* End Card Events */

/* Detaitls Card  */
.details-card {
  border-radius: 15px;
  padding: 15px;

  .card__text p {
    font-size: 1.2rem;
    color: $gray2;

    i {
      color: $orange1;
      font-size: 1rem;
    }
  }
  .card__img {
    img {
      width: 100%;
      max-width: 150px;
    }
  }
}

/* End Detaitls Card  */

/* description */
.description {
  .description__head {
  }
  .description__body {
    border: 1px solid $gray2;
    border-radius: 15px 0 15px 15px;
    overflow: hidden;
    p {
      padding: 15px;
      background-color: $gray3;
      color: $gray2;
      margin-bottom: 0;
      font-size: 16px;
    }
  }
}
/* // description */

/* terms */

.terms {
  .terms__body {
    .terms__list {
      .list__item {
        .item__text {
          margin: 0;
          padding-top: 10px;

          &::before {
            content: "";
            display: inline-block;
            width: 10px;
            height: 10px;
            border-radius: 50%;
            background-color: $orange1;
            vertical-align: -2px;
          }
        }
      }
    }
  }
}

/* // terms */
