}
.bg-gray2 {
  background-color: $gray2;
}
.bg-gray3 {
  background-color: $gray3;
}
.bg-white {
  background-color: $white;
}
.bg-orange1 {
  background-color: $orange1;
}
.bg-orange2 {
  background-color: $orange2 !important;
}
/* background--color */

/* text-color */
.text-gray1 {
  color: $gray1;
}
.text-gray2 {
  color: $gray2;
}
.text-gray3 {
