/* imges */
.img--p1 {
  padding: 3px;
  border-radius: 50%;
  background-color: $white;
  border: 1px solid $gray1;
  width: 100%;
}

/* // imges */

/* radius  */
.rounded-15 {
  border-radius: 15px;
}

/* // radius */

