/* Contact us */
.contact-us {
  .form-control:focus {
    border-color: $orange1;
    box-shadow: 0 0 0 0.2rem rgba(252, 163, 27, 0.32);
  }
  .form-check {
    select {
      background-color: $orange1;
      color: $white;
    }
  }



  select option:hover,select option:checked {
   
   
    background-color: #ddd !important;
    box-shadow: 0 0 10px 100px #905000 inset;
  }
}

