.event-detail {
  .side-carsouel {
    .gallery-top {
      height: 500px;
    }
    .gallery-thumbs {
      height: 100px;
    }
  }

  .time__detail {
    &:nth-last-of-type(1) {
      border-radius: 0 15px 15px 0;
    }
    &:nth-last-of-type(2) {
      border-radius: 15px 0 0 15px;
    }
  }
}

@media screen and #{$maxMobile} {
  .event-detail {
    .side-carsouel {
      .gallery-top {
        height: 500px;
      }
      .gallery-thumbs {
        height: 100px;
      }
    }

    .section__title--main {
      .section-title__circle {
        padding: 2px;
        width: 40px;
        height: 40px;
      }
      .section-title__text {
        font-size: 1.2rem;
      }
    }
    .remaining-time .title {
      margin-top: 0;
      font-size: 1rem;
      text-align: center;
    }
    .remaining-time .time__detail {
      padding: 7px 0;
      font-size: 0.8rem;
    }
    .details-card {
      flex-direction: column-reverse;
      .card__img {
        text-align: center !important;
        margin-bottom: 10px;
      }
      .card__text p {
        text-align: center;
        font-size: 1rem;
      }
    }
  }
}
